'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Textarea } from '@/components/ui/textarea'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Switch } from '@/components/ui/switch'
import { Label } from '@/components/ui/label'

interface DatabaseCollection {
  id: string
  name: string
  documents: number
  createdAt: string
}

interface StorageFile {
  id: string
  name: string
  size: string
  type: string
  uploadedAt: string
}

interface AuthProvider {
  id: string
  name: string
  enabled: boolean
  config: Record<string, any>
}

export default function BackendServicesPage() {
  // Database state
  const [collections, setCollections] = useState<DatabaseCollection[]>([
    { id: '1', name: 'users', documents: 150, createdAt: '2024-01-15' },
    { id: '2', name: 'posts', documents: 89, createdAt: '2024-01-20' },
    { id: '3', name: 'comments', documents: 234, createdAt: '2024-01-22' }
  ])
  const [newCollectionName, setNewCollectionName] = useState('')

  // Storage state
  const [files, setFiles] = useState<StorageFile[]>([
    { id: '1', name: 'profile-pic.jpg', size: '2.3 MB', type: 'image/jpeg', uploadedAt: '2024-01-25' },
    { id: '2', name: 'document.pdf', size: '1.8 MB', type: 'application/pdf', uploadedAt: '2024-01-24' },
    { id: '3', name: 'backup.zip', size: '15.2 MB', type: 'application/zip', uploadedAt: '2024-01-23' }
  ])

  // Authentication state
  const [authProviders, setAuthProviders] = useState<AuthProvider[]>([
    { id: '1', name: 'Email/Password', enabled: true, config: { requireEmailVerification: true } },
    { id: '2', name: 'Google', enabled: false, config: { clientId: '', clientSecret: '' } },
    { id: '3', name: 'GitHub', enabled: false, config: { clientId: '', clientSecret: '' } },
    { id: '4', name: 'Twitter', enabled: false, config: { apiKey: '', apiSecret: '' } }
  ])

  // API Keys state
  const [apiKeys, setApiKeys] = useState([
    { id: '1', name: 'Production API Key', key: 'pk_live_...', created: '2024-01-15', lastUsed: '2024-01-25' },
    { id: '2', name: 'Development API Key', key: 'pk_test_...', created: '2024-01-10', lastUsed: '2024-01-25' }
  ])

  const [error, setError] = useState('')
  const [success, setSuccess] = useState('')

  // Database functions
  const handleCreateCollection = () => {
    if (!newCollectionName.trim()) {
      setError('Collection name is required')
      return
    }

    const newCollection: DatabaseCollection = {
      id: Date.now().toString(),
      name: newCollectionName.trim(),
      documents: 0,
      createdAt: new Date().toISOString().split('T')[0]
    }

    setCollections(prev => [...prev, newCollection])
    setNewCollectionName('')
    setSuccess('Collection created successfully')
    setTimeout(() => setSuccess(''), 3000)
  }

  const handleDeleteCollection = (id: string) => {
    setCollections(prev => prev.filter(col => col.id !== id))
    setSuccess('Collection deleted successfully')
    setTimeout(() => setSuccess(''), 3000)
  }

  // Storage functions
  const handleFileUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]
    if (!file) return

    const newFile: StorageFile = {
      id: Date.now().toString(),
      name: file.name,
      size: `${(file.size / 1024 / 1024).toFixed(1)} MB`,
      type: file.type,
      uploadedAt: new Date().toISOString().split('T')[0]
    }

    setFiles(prev => [...prev, newFile])
    setSuccess('File uploaded successfully')
    setTimeout(() => setSuccess(''), 3000)
  }

  const handleDeleteFile = (id: string) => {
    setFiles(prev => prev.filter(file => file.id !== id))
    setSuccess('File deleted successfully')
    setTimeout(() => setSuccess(''), 3000)
  }

  // Auth functions
  const handleToggleAuthProvider = (id: string) => {
    setAuthProviders(prev =>
      prev.map(provider =>
        provider.id === id
          ? { ...provider, enabled: !provider.enabled }
          : provider
      )
    )
  }

  // API Key functions
  const handleGenerateApiKey = () => {
    const newKey = {
      id: Date.now().toString(),
      name: 'New API Key',
      key: `pk_${Math.random().toString(36).substr(2, 9)}...`,
      created: new Date().toISOString().split('T')[0],
      lastUsed: 'Never'
    }

    setApiKeys(prev => [...prev, newKey])
    setSuccess('API key generated successfully')
    setTimeout(() => setSuccess(''), 3000)
  }

  const handleDeleteApiKey = (id: string) => {
    setApiKeys(prev => prev.filter(key => key.id !== id))
    setSuccess('API key deleted successfully')
    setTimeout(() => setSuccess(''), 3000)
  }

  return (
    <div className="p-6">
      <div className="mb-6">
        <h1 className="text-3xl font-bold">Backend Services</h1>
        <p className="text-muted-foreground">Manage your application's backend infrastructure</p>
      </div>

      {/* Success/Error Messages */}
      {success && (
        <Alert className="mb-4">
          <AlertDescription>{success}</AlertDescription>
        </Alert>
      )}
      {error && (
        <Alert variant="destructive" className="mb-4">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      <Tabs defaultValue="database" className="space-y-4">
        <TabsList className="grid w-full grid-cols-4">
          <TabsTrigger value="database">Database</TabsTrigger>
          <TabsTrigger value="storage">Storage</TabsTrigger>
          <TabsTrigger value="auth">Authentication</TabsTrigger>
          <TabsTrigger value="api">API Keys</TabsTrigger>
        </TabsList>

        {/* Database Tab */}
        <TabsContent value="database" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>Database Collections</CardTitle>
              <div className="flex gap-2">
                <Input
                  placeholder="Collection name"
                  value={newCollectionName}
                  onChange={(e) => setNewCollectionName(e.target.value)}
                  onKeyPress={(e) => e.key === 'Enter' && handleCreateCollection()}
                />
                <Button onClick={handleCreateCollection}>Create Collection</Button>
              </div>
            </CardHeader>
            <CardContent>
              <div className="space-y-2">
                {collections.map((collection) => (
                  <div key={collection.id} className="flex items-center justify-between p-3 border rounded-lg">
                    <div>
                      <h3 className="font-medium">{collection.name}</h3>
                      <p className="text-sm text-muted-foreground">
                        {collection.documents} documents • Created {collection.createdAt}
                      </p>
                    </div>
                    <div className="flex gap-2">
                      <Button variant="outline" size="sm">View</Button>
                      <Dialog>
                        <DialogTrigger asChild>
                          <Button variant="destructive" size="sm">Delete</Button>
                        </DialogTrigger>
                        <DialogContent>
                          <DialogHeader>
                            <DialogTitle>Delete Collection</DialogTitle>
                            <DialogDescription>
                              Are you sure you want to delete the "{collection.name}" collection? This action cannot be undone.
                            </DialogDescription>
                          </DialogHeader>
                          <DialogFooter>
                            <Button variant="outline">Cancel</Button>
                            <Button variant="destructive" onClick={() => handleDeleteCollection(collection.id)}>
                              Delete
                            </Button>
                          </DialogFooter>
                        </DialogContent>
                      </Dialog>
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        {/* Storage Tab */}
        <TabsContent value="storage" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>File Storage</CardTitle>
              <div>
                <input
                  type="file"
                  onChange={handleFileUpload}
                  className="hidden"
                  id="file-upload"
                />
                <Button asChild>
                  <label htmlFor="file-upload" className="cursor-pointer">
                    Upload File
                  </label>
                </Button>
              </div>
            </CardHeader>
            <CardContent>
              <div className="space-y-2">
                {files.map((file) => (
                  <div key={file.id} className="flex items-center justify-between p-3 border rounded-lg">
                    <div>
                      <h3 className="font-medium">{file.name}</h3>
                      <p className="text-sm text-muted-foreground">
                        {file.size} • {file.type} • Uploaded {file.uploadedAt}
                      </p>
                    </div>
                    <div className="flex gap-2">
                      <Button variant="outline" size="sm">Download</Button>
                      <Button variant="destructive" size="sm" onClick={() => handleDeleteFile(file.id)}>
                        Delete
                      </Button>
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        {/* Authentication Tab */}
        <TabsContent value="auth" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>Authentication Providers</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {authProviders.map((provider) => (
                  <div key={provider.id} className="flex items-center justify-between p-3 border rounded-lg">
                    <div>
                      <h3 className="font-medium">{provider.name}</h3>
                      <p className="text-sm text-muted-foreground">
                        {provider.enabled ? 'Enabled' : 'Disabled'}
                      </p>
                    </div>
                    <div className="flex items-center gap-4">
                      <div className="flex items-center space-x-2">
                        <Switch
                          id={`provider-${provider.id}`}
                          checked={provider.enabled}
                          onCheckedChange={() => handleToggleAuthProvider(provider.id)}
                        />
                        <Label htmlFor={`provider-${provider.id}`}>Enable</Label>
                      </div>
                      <Button variant="outline" size="sm">Configure</Button>
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        {/* API Keys Tab */}
        <TabsContent value="api" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>API Keys</CardTitle>
              <Button onClick={handleGenerateApiKey}>Generate New Key</Button>
            </CardHeader>
            <CardContent>
              <div className="space-y-2">
                {apiKeys.map((apiKey) => (
                  <div key={apiKey.id} className="flex items-center justify-between p-3 border rounded-lg">
                    <div>
                      <h3 className="font-medium">{apiKey.name}</h3>
                      <p className="text-sm text-muted-foreground font-mono">{apiKey.key}</p>
                      <p className="text-xs text-muted-foreground">
                        Created {apiKey.created} • Last used {apiKey.lastUsed}
                      </p>
                    </div>
                    <div className="flex gap-2">
                      <Button variant="outline" size="sm">Copy</Button>
                      <Button variant="destructive" size="sm" onClick={() => handleDeleteApiKey(apiKey.id)}>
                        Delete
                      </Button>
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  )
}
